import styles from './MyReservation.module.scss';
import classNames from 'classnames/bind';
import { Container, Row, Col } from 'react-bootstrap';
import { useState } from 'react';
import Button from '../../components/Button';

const cx = classNames.bind(styles);
function MyReservation() {
    const [isChecked, setIsChecked] = useState(false);

  const handleCheckboxChange = () => {
    setIsChecked(!isChecked);
  };
  

  const title= `Hy:\nNu hy hoc thay i sm hn 7 ngy trc ngy nhn phng,khng c khon ph hy phng no phi thanh ton.\nNu hy hoc thay i trong vng 7 ngy trc ngy n, m u tin phi thanh ton.\nTrong trng hp khng n, tng gi tr tin t phng phi thanh ton.\n-Nhn phng: 14:00 \n- Tr phng: 12:00`;

    
    return (
    <>
        <div className={cx('MyReservation')}>
            <Container fluid="md" className={cx('item-wrapper')}>
                <Row>
                <strong> Chi tit t phng </strong>
                   <div className={cx('section-1')} >
                    <p>Ngy nhn: Th 5, Thng 7 13, 2023 t 14:00</p>
                    <p>Ngy tr:  Th 6, Thng 7 14, 2023 cho n 12:00 </p>
                    <a href='/xem-gia'>(Bn c mun chuyn ngy du lch?)</a>
                    <div className={cx('content')} >
                        <p>Superior Double</p>
                        <div className={cx('detail')} >
                            <p>Chi tit: 1 phng, 1 m, 2 ngi ln  bao gm trong gi</p>
                            <p>Gi: 1.790.000 VN</p>
                        </div>
                      
                    </div>
                   </div>
                </Row>
                <Row>
                <strong> Dch v thm </strong>
                   <div className={cx('section-2')} >
                    <div className={cx('content')} >
                     <p>Extra bed</p>
                     <div className={cx('detail')} >
                        <div className={cx('text-item')}>
                        <input  type="number"  placeholder='0' min="0" max="2"/>    <label> 460.000 </label>
                        </div>
                        
                        <button type="button" className="btn btn-secondary" data-toggle="tooltip"data-placement="top" title={title}>
                                Chnh sch t phng
                        </button>
                    
                    </div>
                    </div>
                    <div className={cx('content')} >
                     <p>Airport Dropoff</p>
                     <div className={cx('detail')} >
                        <div className={cx('text-item')}>
                        <input  type="number"  placeholder='0'  min="0" max="2"/>    <label> 460.000 </label>
                        </div> 
                       
                                <button type="button" className="btn btn-secondary" data-toggle="tooltip" data-placement="top" title={title}>
                                Chnh sch t phng
                                </button>
                                
                        
                    </div>

                    <p>Airport Pickup</p>
                    <div className={cx('detail')} >
                        <div className={cx('text-item')}>
                        <input  type="number"  placeholder='0'  min="0" max="2"/>    <label> 460.000 </label>
                        </div>
                      
                                <button type="button" className="btn btn-secondary" data-toggle="tooltip" data-placement="top" title={title}>
                                Chnh sch t phng
                                </button>
                               
                        
                    </div>
                    </div>
                   </div>
                </Row>
                <Row className={cx('section-3-4')} >
                    <Col>
                    <strong> Thng tin khch </strong>
                    <div className={cx('section-3')} >
                    <Row>
                        <Col >
                        <p>Tn *</p>
                        <input type="text" id="name" name="name"  />
                        </Col>
                        <Col>
                        <p>H *</p>
                        <input type="text" id="surname" name="surname"/>
                        </Col>
                    </Row>
                    <Row>
                        <p>Email *</p>
                        <input type="text" className={cx('email')} id="email" name="email" />
                    </Row>
                    <Row className={cx('phone')}>
                        <Col  >
                        <p>S in thoi lin lc *</p>
                        <input type="text" id="phone" name="phone"  />
                        </Col>
                        <Col>
                        <p>Quc gia *</p>
                        <input type="text" id="nation" name="nation" />
                        </Col>
                    </Row>
                    </div>
                    </Col>
                    <Col>
                    <strong> Tng gi tr </strong>
                    <div className={cx('section-4')} >
                        <div className={cx('content')} >
                            <p>Gi phng</p>
                            <p> 1.790.000</p>
                        </div>
                        <div className={cx('content')} >
                            <p>Dch v thm</p>
                            <p> 0</p>
                        </div>
                        <div className={cx('content')} >
                            <p>Tng gi</p>
                            <p> 1.790.000</p>
                        </div>
                        <div className={cx('check-item')} >
                        <input className="form-check-input" type="checkbox" checked={isChecked}
                        onChange={handleCheckboxChange} name="flexCheckbox" id="flexCheckbox"/>    
                            <p>Vui lng c k v ng  vi iu khon t phng bng cch nh du vo  bn cnh.</p>
                        </div>
                        <Button className={cx('btn')} filled_1 disable={!isChecked} to={'/thanh-toan'} >Xc nhn t phng</Button>
                    </div>
                    </Col>
                </Row>
            </Container>
        </div>
    </>
    );
}
export default MyReservation;
