import styles from './RestaurentAndBar.module.scss';
import classNames from 'classnames/bind';
import { rest1, rest2, rest3, bar1,bar2,bar3, buffet1,  rest4, buffet2} from '../../assets/images/restaurent';
import { Container, Row, Col, Carousel } from 'react-bootstrap';
import Button from '../../components/Button';

const cx = classNames.bind(styles);
function RestaurentAndBar() {
    const heroImages = [rest1, bar1, buffet1, rest2];
    return <>
       <div className={cx('hero')}>
       <Carousel
                className={cx('item-wrapper')}
                controls={false}
                wrap={true}
                indicators={false}
                interval={10000}
            >
                {heroImages.map((image, index) => (
                    <Carousel.Item key={index} className={cx('item')}>
                        <div
                            style={{
                                backgroundImage: `url(${image})`,
                                backgroundSize: 'cover',
                                backgroundPosition: 'center',
                                backgroundRepeat: 'no-repeat',
                                maxWidth: '100%',
                                height: '100%',
                                }}
                            ></div>
                    </Carousel.Item>
                    ))}
        </Carousel>
        </div>
        <div className={cx('section-1')}>
            <Container fluid="md">
                <Row className={cx('px-0','content')}>
                    <Col className={cx('px-0')} >
                    <img src={buffet1} alt="buffet" className={cx('image')}/>
                    </Col>
                    <Col className={cx('px-0')}>         
                    <strong
                            className={cx('heading')}
                        >{`NH HNG BUFFET`}</strong>
                        <p className={cx('content-text')}> Ni hon ho  tn hng s sang trng v thoi mi tuyt nh!</p>
                        <hr className={cx('hr')}/>
                        <p className={cx('content-text')}>Ta lc tng 5, vi gc nhn p hng ra i l Nguyn Hu, Stellar Hotel ni bt vi hn 60 mn n ca ng bng sng Cu Long c ch bin t m ca gm m, bnh kht, gi cun, u h, ch, ht vt ln v nc mt cng nh cc mn hi sn nng. Ngoi ra, thc khch cn c thng thc cc bui trnh din nhc c truyn thng trong khi thng thc ba n.</p>
                        <p>Gi m ca:</p>
                        <ul>
                            <li>Ba sng: t 6 gi sng n 9 gi sang hng ngy.</li>
                            <li>n tra: t 11 gi sng n 2 gi chiu hng ngy.</li>
                            <li>Ba ti: t 6 gi ti n 9 gi 30 chiu hng phc.</li>
                        </ul>
                    </Col>
                </Row>
            </Container>    
        </div>
        <div className={cx('section-2')}>
            <strong className={cx('heading')} >HNH NH</strong>
            <Container fluid className={cx('px-0')}>
                <Row className={cx('px-0','content')}>
                    <Col className={cx('px-0')} >
                    <img src={rest4} alt="rest4" className={cx('image')}/>
                    </Col>
                    <Col className={cx('px-0')} >
                    <img src={buffet2} alt="buffet2" className={cx('image')}/>
                    </Col>
                    <Col className={cx('px-0')} >
                    <img src={rest2} alt="rest2" className={cx('image')}/>
                    </Col>
                </Row>
                <Row className={cx('px-0','content')}>
                    <Button outline_2 className={cx('btn')} to={'/lien-he'}>T BN NGAY</Button> 
                </Row> 
                </Container>
        </div>
        <div className={cx('section-3')}>
        
            <div>
            <img src={bar2} alt="bar2" className={cx('image')}/>
            </div>
            <div className={cx('content-wrapper')}>
                     <strong className={cx('heading')}>QUN BAR</strong>  
                     <p><strong>Stellar Hotel l mt trong nhng hp m ni ting nht trong thnh ph.</strong></p>
            </div>
          
        </div>
        <div className={cx('section-1','section-4')}>
            <Container fluid="md">
                <Row className={cx('px-0','content')}>
                    <Col className={cx('px-0')} >
                    <img src={bar1} alt="bar" className={cx('image')}/>
                    </Col>
                    <Col className={cx('px-0')}>         
                    <strong
                                className={cx('heading')}
                    >{`STELLAR BAR`}</strong>
                       <hr className={cx('hr')}/>
                        <p className={cx('content-text')}>Stellar Hotel mang khng kh ca cu lc b bin. Thng xuyn thu ht ngi nc ngoi n mc bnh bao v ngi dn a phng n y gii tr th gin. Qun bar trn tng thng ny c trang b rt nhiu cy nhit i, trang tr ni tht theo tng mu en trng. Sn nhy rng v quy bar y  d tr trn boong chnh phc v y v nhit tnh cc khch hng n y.</p>
                        <p className={cx('content-text')}>Vi mt ci nhn tuyt p ca trung tm Si Gn. Chill Skybar l mt trong nhng hp m ni ting nht trong thnh ph.  y bn s chng kin mt Si Gn cc k p vi thng thc nhc dance hay hip hop si ng y hp dn. Nhng  ung  y l rt tn km, chi ph ca mt cocktail gn 20$. B vo  ng b l gi hnh phc tuyt vi 17:30 n 20:00, cc loi thc n v  ung s c gim mt na gi.</p>
                    </Col>
                 </Row>
            </Container>
        </div>
        <div className={cx('section-2','section-5')}>
            <strong className={cx('heading')} >HNH NH</strong>
            <Container fluid className={cx('px-0')}>
                <Row className={cx('px-0','content')}>
                    <Col className={cx('px-0')} >
                    <img src={bar3} alt="rest4" className={cx('image')}/>
                    </Col>
                    <Col className={cx('px-0')} >
                    <img src={rest3} alt="buffet2" className={cx('image')}/>
                    </Col>
                    <Col className={cx('px-0')} >
                    <img src={bar1} alt="rest2" className={cx('image')}/>
                    </Col>
                </Row>
                <Row className={cx('px-0','content')}>
                    <Button outline_2 className={cx('btn')} to={'/lien-he'}>T BN NGAY</Button> 
                </Row> 
                </Container>
        </div>
    </>;
}

export default RestaurentAndBar;