import { Carousel, Container, Row, Col } from 'react-bootstrap';
import React from 'react';
import classNames from 'classnames/bind';
import { useState } from 'react';
import Modal from 'react-bootstrap/Modal';

import Image from '../../components/Image';
//import {InfoRoomRight, InfoRoomLeft} from './InfoRoom';
import {
    Deluxe1,
    Deluxe2,
    Executive1,
    Executive2,
    Suite1,
    Suite2,
    Superior1,
    Superior2,
    pic1,
    pic2,
    pic3,
    introRoom,
} from '../../assets/images/bookroom';
import styles from './BookRoom.module.scss';
import Button from '../../components/Button';

const cx = classNames.bind(styles);
function BookRoom() {
    const images = [pic1, pic2, pic3];
    const room = [
        {
            id: 1,
            name: 'Phng Superior Double Or Twin',
            acreage: '23m2',
            typebed: 'Ging i hoc 02 ging n',
            capacity: '2 ngi ln',
            view: 'Phng khng c ca s',
            description:
                'Phng ngh Stellar - Superior Double Or Twin mang n s m cng bi nt duyn dng ca lch s v nhng tin nghi hin i nht. Vi sn nh kt hp g v gch p, ging i thoi mi v  ni tht trang nh, tt c to nn s cn bng gia c in v hin ai.',
            images: [Superior1, Superior2],
        },
        {
            id: 2,
            name: 'Phng Deluxe Double',
            acreage: '23m2',
            typebed: 'Ging i',
            capacity: '2 ngi ln',
            view: 'Phng khng c ca s',
            description:
                'Phng ngh Stellar - Deluxe Double, cn phng m p ny mang li s hon ho trong k ngh ti Si Gn. Khng gian lm vic kt ni vi cc tin nghi phng ngh, cng ging ng vi b chn lng v m i, cht liu cao cp cng dch v phc v phng tn ni mang li s thoi mi v th gin ti a',
            images: [Deluxe1, Deluxe2],
        },
        {
            id: 3,
            name: 'Phng Executive City View',
            acreage: '40m2',
            typebed: '02 Ging n',
            capacity: '2 ngi ln',
            view: 'Thnh ph',
            description:
                'Vi hai ca s ln cho quang cnh tuyt vi nhin ra thanh ph, Phng Executive City View mang li cho qu khch mt khng gian thong ng, rng m. c trang tr bng sn g kt hp gch, nhng mn  ni tht phng pht phong cch ng Dng kt hp nhng tin nghi hin i to nn mt tng th hi ho, ng i m qu khch c th tri nghim  cm nhn nt p Si Gn chun xc nht.',
            images: [Executive1, Executive2],
        },
        {
            id: 4,
            name: 'Phng Suite Garden',
            acreage: '40m2',
            typebed: '01 ging queen',
            capacity: '2 ngi ln',
            view: 'Thnh ph',
            description:
                'Phng Suite Garden c phi hp phong cach hin ai vi cam hng t cy xanh, rng ri, hon ho cho cc k ngh cui tun hay chuyn khm ph ca quy khach. Ban cng rng va c sp xp  quy khach lun cam nhn c khng khi trong lanh, gio nhe lay va bong mat t cac tan cy. Loi phng ngh ny p ng y  nhu cu ngm nhn ng ph, tn hng nhng giy phut m minh trong bn tm b suc.',
            images: [Suite1, Suite2],
        },
    ];
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <>
            <div className={cx('hero')}>
                <Carousel
                    className={cx('item-wrapper')}
                    controls={false}
                    wrap={true}
                    indicators={false}
                    interval={3000}
                >
                    {images.map((image, index) => (
                        <Carousel.Item key={index} className={cx('item')}>
                            <div
                                style={{
                                    backgroundImage: `url(${image})`,
                                    backgroundSize: 'cover',
                                    backgroundPosition: 'center',
                                    backgroundRepeat: 'no-repeat',
                                    maxWidth: '100%',
                                    height: '100%',
                                }}
                            ></div>
                        </Carousel.Item>
                    ))}
                </Carousel>
                <Container fluid="md">
                    <div className={cx('content-wrapper')}>
                        <div className={cx('input-date')}>
                            <span className={cx('date')}>
                                Check in
                                <input type="date"></input>
                            </span>
                            <span className={cx('date')}>
                                Check out
                                <input type="date"></input>
                            </span>
                        </div>

                        <Button to={'/xem-gia'} className={cx('btn')}>
                            CHECK AVAILABILITY
                        </Button>
                    </div>
                </Container>
            </div>
            <Container fluid="md">
                <Row className={cx('Intro')}>
                    <Col>
                        <div className={cx('ContentIntro')}>
                            <h1>Phng ngh</h1>
                            <p>Ni hon ho  tn hng s sang trng v thoi mi tuyt nh!</p>
                            <hr width="30%" align="center" />
                            <p>
                                Vi v tr trung tm ti tri tim Si Gn, Stellar Hotel khng ch mang n s thun
                                tin trong vic di chuyn v khm ph cc im tham quan ni ting ca thnh ph, m cn
                                to nn mt khng gian yn tnh v thanh bnh gia s si ng ca  th.
                            </p>
                            <p>
                                Kin trc c o v thit k tinh t, Stellar Hotel em n cho qu khch khng gian
                                sng ng cp v y phong cch. T cc phng ngh sang trng n cc suite cao cp, mi
                                khng gian c trang b tin nghi hin i v nhng chi tit t m  mang n s thoi
                                mi ti a cho khch hng
                            </p>
                        </div>
                    </Col>
                    <Col>
                        <Image className={cx('IntroImage')} src={introRoom} alt="introRoom" />
                    </Col>
                </Row>
            </Container>
            <div className={cx('Room')}>
                {room.map((item, index) => (
                    <Container className={cx('container')} fluid="md" key={item.id}>
                        <Row className={cx('row')} style={{ flexDirection: item.id % 2 === 0 ? 'row-reverse' : 'row' }}>
                            <Col>
                                <Carousel
                                    className={cx('item-wrapper')}
                                    controls={false}
                                    wrap={true}
                                    indicators={false}
                                    interval={3000}
                                >
                                    {item.images.map((image, index) => (
                                        <Carousel.Item key={index} className={cx('item')}>
                                            <div
                                                style={{
                                                    backgroundImage: `url(${image})`,
                                                    backgroundSize: 'cover',
                                                    backgroundPosition: 'center',
                                                    backgroundRepeat: 'no-repeat',
                                                    maxWidth: '100%',
                                                    height: '100%',
                                                }}
                                            ></div>
                                        </Carousel.Item>
                                    ))}
                                </Carousel>
                            </Col>
                            <Col>
                                <h1>{item.name}</h1>
                                <hr width="30%" align="center" />
                                <Row>
                                    <Col>
                                        <ul>
                                            <li>Din tch: {item.acreage}</li>
                                            <li>Loi ging: {item.typebed}</li>
                                        </ul>
                                    </Col>
                                    <Col>
                                        <ul>
                                            <li>Sc cha: {item.capacity}</li>
                                            <li>Hng nhn: {item.view}</li>
                                        </ul>
                                    </Col>
                                </Row>
                                <p>{item.description}</p>
                                <h3>Tin nghi phng:</h3>
                                <Row>
                                    <Col>
                                        <ul>
                                            <li>My iu ho</li>
                                            <li>Dch v phng 24 gi</li>
                                            <li>Truy cp internet min ph</li>
                                            <li>in thoi IDD</li>
                                            <li>My iu ho</li>
                                            <li>My sy tc</li>
                                            <li>Bn lm vic</li>
                                        </ul>
                                    </Col>
                                    <Col>
                                        <ul>
                                            <li>Bn tm v vi sen</li>
                                            <li>Min ph nc , c ph, tr</li>
                                            <li>Kt st trong phng</li>
                                            <li>Dch v git i/ git kh</li>
                                            <li>TV truyn hnh v tinh/ cp</li>
                                            <li>B bn chi nh rng/ du gi</li>
                                            <li>Trang phc phng tm/ khn tm/ dp</li>
                                        </ul>
                                    </Col>
                                </Row>
                                <Button filled_1 className={cx('btn')} onClick={handleShow}>
                                    {' '}
                                    t phng
                                </Button>
                            </Col>
                        </Row>
                        <Modal size="xl" show={show} onHide={handleClose}>
                            <Modal.Header closeButton></Modal.Header>
                            <div className={cx('Search')}>
                                <div className={cx('date')}>
                                    Ngy nhp phng:
                                    <input type="date" />
                                    Ngy tr phng:
                                    <input type="date" />
                                </div>
                                <div className={cx('date')}>
                                    Ngi ln:
                                    <input type="number" min="0" max="5" placeholder="1" />
                                    Tr em:
                                    <input type="number" min="0" max="5" placeholder="1" />
                                </div>
                                <Button className={cx('btn')} to={'/xem-gia'}>
                                    t phng
                                </Button>
                            </div>
                        </Modal>
                    </Container>
                ))}
            </div>
        </>
    );
}

export default BookRoom;
