import styles from './Footer.module.scss';
import classNames from 'classnames/bind';

import React from 'react';
import { Link } from 'react-router-dom';
import Image from '../../../Image';
import images from '../../../../assets/images';
import Button from '../../../Button';

const cx = classNames.bind(styles);

const TITLE_FOOTER = [
    {
        title: 'V Stellar',
        to: '/ve-Stellar',
    },
    {
        title: 'Phng ngh',
        to: '/dat-phong',
    },
    {
        title: 'Nh hng',
        to: '/nha-hang-quan-bar',
    },
    {
        title: 'Hi tho & s kin',
        to: '/hoi-thao-su-kien',
    },
    {
        title: 'Tin ch',
        to: '/tien-ich',
    },
    {
        title: 'Lin h',
        to: '/lien-he',
    },
];

function Footer() {
    return (
        <h1 className={cx('wrapper')}>
            <div className={cx('back-ground')}>
                <Link to={'/'}>
                    <Image src={images.logo} className={cx('logo')} />
                </Link>
                <div className={cx('menu-item')}>
                    {TITLE_FOOTER.map((item, index) => (
                        <Button className={cx('btn')} none_1 to={item.to} key={index}>
                            {item.title}
                        </Button>
                    ))}
                </div>
            </div>
            <div className={cx('thanh-ngang')}></div>
            <div className={cx('info')}>
                <div>Website chnh thc ca khch sn Stellar</div>
                <div>S in thoi: (+84) 9465412XX</div>
                <div>Email: info@stellar.com.vn</div>
                <div>a ch: 01 V Vn Ngn, P. Linh Chiu, Q. Th c, TP. H Ch Minh</div>
            </div>
        </h1>
    );
}

export default React.memo(Footer);
