import styles from './ConferenceEvents.module.scss';
import classNames from 'classnames/bind';
import { Carousel } from 'react-bootstrap';
import { pic1, pic2, pic3, pic4 } from '../../assets/images/conference';
import Button from '../../components/Button';

const cx = classNames.bind(styles);
function ConferenceEvents() {
    const images = [pic1, pic2, pic3, pic4];
    return (
        <>
            <div className={cx('hero')}>
                <h1>
                    <strong>TRUNG TM HI NGH</strong>
                </h1>
                <Carousel
                    className={cx('item-wrapper')}
                    controls={false}
                    wrap={true}
                    indicators={false}
                    interval={3000}
                >
                    {images.map((image, index) => (
                        <Carousel.Item key={index} className={cx('item')}>
                            <div
                                style={{
                                    backgroundImage: `url(${image})`,
                                    backgroundSize: 'cover',
                                    backgroundPosition: 'center',
                                    backgroundRepeat: 'no-repeat',
                                    maxWidth: '100%',
                                    height: '100%',
                                }}
                            ></div>
                        </Carousel.Item>
                    ))}
                </Carousel>
            </div>
            <div className={cx('content-wrapper')}>
                <hr width="20%" align="center" />
                <p>
                    Trung tm hi ngh Stellar chnh l la chn ng cp dnh cho cc doanh nghip c thit k vi
                    phong cch tinh t v sang trng.
                </p>
                <p>
                    Stellar l mt trung tm t chc hi ngh ng cp quc t ti TP.H Ch Minh vi li kin trc tinh
                    t, c o cng mt khng gian rng ln, cht lng dch v chuyn nghip ng cp hng u. c
                    pht trin bi i ng nhn vin qun l chuyn nghip c o to bo bn theo ng cp quc t
                    nhm mang n s hi lng cho khch hng.
                </p>
                <p>
                     p ng nhng yu cu ca khch hng, cht lng phc v, nhng mong mun, nhu cu v sn phm
                    ng thi tht cht mi quan h gia khch hng v doanh nghip, gia ng nghip vi nhau. Vic t
                    chc hi ngh hay cc l k nim, tri n khch hng lun ng mt vai tr rt quan trng i vi mi
                    doanh nghip. y cng l c hi  doanh nghip qung b thng hiu ca mnh rng ri, thu ht
                    thm c mt lng ln khch hng tim nng.
                </p>
            </div>
            <div className={cx('session-1')}>
                <div className={cx('content')}>
                    <h1>LIN H NHN BO GI</h1>
                    <h3>in thng tin lin h t hi ngh - s kin  c t vn v nhn gi tt nht</h3>
                    <input className={cx('name')} type="text" placeholder="H v tn" />
                    <input className={cx('email')} type="text" placeholder="Email" />
                    <textarea name="subject" className={cx('email', 'mess')} placeholder="Li nhn..."></textarea>
                    <Button filled_1>Gi thng tin</Button>
                </div>
            </div>
        </>
    );
}

export default ConferenceEvents;
