import { Carousel, Container, Row, Col } from 'react-bootstrap';
import React, { useState } from 'react';
import classNames from 'classnames/bind';
import {pic1,pic2,pic3, Suite1} from "../../assets/images/bookroom";
import styles from './ViewPrice.module.scss';
import Image from '../../components/Image';
const cx = classNames.bind(styles);

function ViewPrice() {
    const images = [pic1, pic2, pic3];
    const [selectedValue, setSelectedValue] = useState('1');

    const handleSelectionChange = (event) => {
        setSelectedValue(event.target.value);
    }
    const isSelectionOne = selectedValue === '1';

    return <>
    <div className={cx('hero')}>
            <Carousel
                className={cx('item-wrapper')}
                controls={false}
                wrap={true}
                indicators={false}
                interval={3000}
            >
                {images.map((image, index) => (
                    <Carousel.Item key={index} className={cx('item')}>
                        <div
                            style={{
                                backgroundImage: `url(${image})`,
                                backgroundSize: 'cover',
                                backgroundPosition: 'center',
                                backgroundRepeat: 'no-repeat',
                                maxWidth: '100%',
                                height: '100%',
                            }}
                        ></div>
                    </Carousel.Item>
                ))}
            </Carousel>
        </div>
        <Container fluid className={cx('date')}>    
            <Row>
                <Col className={cx('col')}>
                <input id="date-checkin" type="date" />
                </Col>
                <Col className={cx('col')}>
                <input id="date-checkin" type="date" />
                </Col>
                <Col className={cx('col')} >
                <a className={`btn btn-primary ${isSelectionOne ? 'disabled' : ''}`} href="/dat-cho-cua-toi" role="button">
                    THANH TON
                </a>
                </Col>
            </Row>  
        </Container>
            <div className={cx('ListRoom')}>
            <Container  fluid="md" >    
            <Row className={cx('Room')}>
                <Col className={cx('col')}>
                    <Image className={cx('ImageRoom')} src={Suite1} alt="imageRoom" />
                </Col>
                <Col className={cx('col')}>
                    <Row className={cx('InfoRoom')}>
                            <h1>Phng Suite Garden</h1>
                        <p>Phng Suite Garden c phi hp phong cach hin ai vi cam hng t cy xanh, rng ri, hon ho cho cc k ngh cui tun hay chuyn khm  ph ca quy khach. Ban cng rng va c sp xp  quy khach lun cam  nhn c khng khi trong lanh, gio nhe lay va bong mat t cac tan cy
                        </p>
                        <div style={{display:' flex'}}>
                        <p><b>Din tch: </b> 40 m2 </p> 
                        <p><b>Loi ging:</b> 1 ging queen</p> 
                        </div>
                        <a href='/dat-phong'><i>Xem cc tin ch</i></a>
                        <hr  width="30%" align="center"  />
                    </Row>
                    <Row style={{alignItems: "center"}}>
                        <Col>
                            <p><i style={{color:"red", fontSize:"1.3rem"}} >Cn 2 phng trng</i></p>
                        </Col>
                        <Col>
                            <p>Gi: 1.790.000 VN</p>
                        </Col>
                        <Col>
                            <div className="input-group mb-3">
                                    <select onChange={handleSelectionChange} value={selectedValue} >
                                    <option value="1" >0 Phng</option>
                                    <option value="2">1 Phng</option>
                                    <option value="3">2 Phng </option>
                                    <option value="4">3 Phng </option>
                                    <option value="5">4 Phng </option>
                                    <option value="6">5 Phng </option>
                                </select>
                            </div>
                        </Col>
                    </Row>
                </Col>  
            </Row>  
        </Container>
            </div>
       
    </>;
}

export default ViewPrice;
